import React, { useState, useEffect } from "react";
import {
  Box,
  Grid,
  TextField,
  Checkbox,
  FormControlLabel,
  Typography,
  Button,
  Paper,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
} from "@mui/material";
import HeaderBar from "./HeaderBar";
import AdresseSection from "./AdresseSection";
import FournisseurSection from "./FournisseurSection";
import CheckboxSection from "./CheckboxSection";
import ProprieteDemande from "./ProprieteDemande"; // Import du nouveau composant
import LignesEngagements from "./LignesEngagements";
import InformationLivraison from "./InformationLivraison";
import { newRow, racineAPI } from "../_config/config";

const FormulaireDemande = ({ typeDemande, setTypeDemande }) => {
  const [formData, setFormData] = useState({
    adresseLivraison: "",
    adresseFacturation: "",
    fournisseur: "",
    numeroPiece: "",
    copieDocument: false,
    typeDemande: "",
    exerciceBudgetaire: "",
    services: "",
    budgetsActions: "",
    budgetInitial: "0.00 ",
    budgetRestant: "0.00 ",
    dateReception: "",
    descriptionDemande: "",
    justification: "",
    pieceJointe: [],
    lignesTransversales: false, // Deuxime checkbox
  });
  // const [lignesEngagement, setLignesEngagement] = useState([
  //   newRow
  // ]);

  const currentYear = new Date().getFullYear();
  const [lignesEngagement, setLignesEngagement] = useState({
    [currentYear]: [newRow],
  });

  const [open, setOpen] = useState(false); // tat pour la popup
  const [budgetInitial, setBudgetInitial] = useState("0.00 ");
  const [budgetRestant, setBudgetRestant] = useState("0.00 ");
  const [rowBudgetsInitial, setRowBudgetsInitial] = useState([]);
  const [filteredBudgets, setFilteredBudgets] = useState([]); // Budgets filtrs

  const [budgetSelectionManuelle, setBudgetSelectionManuelle] = useState(false);

  const [nom, setNom] = useState([]);
  const [selectedFiles, setSelectedFiles] = useState([]);

  const [montantsBudget, setMontantsBudget] = useState({
    montant_initial: "0.00 ",
    montant_restant: "0.00 ",
  });

  const [categoriePrincipale, setCategoriePrincipale] = useState(null);

  const [validationPossible, setValidationPossible] = useState(true);

  useEffect(() => {
    if (formData.lignesTransversales) {
      const annee1 = formData.exerciceBudgetaire;
      const annee2 = parseInt(annee1) + 1;

      setLignesEngagement((prev) => ({
        ...prev,
        [annee2]: prev[annee2] || [newRow], // Ne recre pas si dj prsent
      }));
    }
  }, [formData.lignesTransversales, formData.exerciceBudgetaire]);

  useEffect(() => {
    fetch(`${racineAPI}recuperer_user_id.php`)
      .then((response) => response.json())
      .then((data) => {
        setNom(data);
      })
      .catch((error) =>
        console.error(
          "Erreur lors de la rcupration de l'utilisateur connect:",
          error
        )
      );
  }, []);
  // useEffect(() => {
  //   fetch("https://armoires.zeendoc.com/vaincre_la_mucoviscidose/_ClientSpecific/66579/data.php")
  //     .then((response) => response.json())
  //     .then((data) => {

  //       setFilteredBudgets(data);
  //     })
  //     .catch((error) => console.error("Erreur lors de la rcupration des budgets :", error));
  // }, []);

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const jsonData = urlParams.get("json");
    const action = urlParams.get("action");

    if (jsonData) {
      try {
        const parsedData = JSON.parse(jsonData);

        // Incrmenter le numro de pice uniquement si l'action est "duplicate"
        if (action === "duplicate" && parsedData.numeroPiece) {
          const currentNumeroPiece = parsedData.numeroPiece;
          const prefix = currentNumeroPiece.slice(0, -6); // Rcupre tout sauf les 6 derniers caractres
          const sequence = parseInt(currentNumeroPiece.slice(-6)) + 1; // Incrmente la squence
          const newNumeroPiece = `${prefix}${sequence
            .toString()
            .padStart(6, "0")}`; // Reformate avec les zros

          parsedData.numeroPiece = newNumeroPiece;
          // Met  jour le numro de pice
        }

        // Mettre  jour les donnes du formulaire
        setFormData((prev) => ({
          ...prev,
          ...parsedData,
        }));

        // Mettre  jour les lignes d'engagement
        if (parsedData.lignesEngagement) {
          setLignesEngagement([...parsedData.lignesEngagement]);
        }
      } catch (error) {
        console.error("Erreur lors du parsing du JSON :", error);
      }
    }
  }, []);

  const today = new Date().toLocaleDateString("fr-FR", {
    day: "2-digit",
    month: "2-digit",
    year: "numeric",
  });

  const handleFormDataChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });

    if (name === "budgetsActions") {
      setBudgetSelectionManuelle(true);
    }
  };

  const handleCheckboxChange = (e) => {
    const { checked, name } = e.target;
    const isChecked = checked;
    setFormData((prevData) => ({
      ...prevData,
      [name]: isChecked,
    }));
  };

  const handleFileChange = (event) => {
    const files = Array.from(event.target.files);

    // Mise  jour du state formData pour conserver les fichiers
    setFormData((prevData) => ({
      ...prevData,
      pieceJointe: files,
    }));

    // Mise  jour de selectedFiles pour l'affichage
    setSelectedFiles(files);
  };

    const polesMap = {
    DG: "0",
    PAF: "1",
    POGEMOB: "2",
    PCOM: "3",
    PRECH: "4",
    PDONNEES: "5",
    PDONNEESDESANTE: "5",
    PSANTE: "5",
    PQDV: "6",
  };

  // Fonction pour obtenir la date actuelle au format YYMMDD
  const getCurrentDate = () => {
    const today = new Date();
    const year = today.getFullYear().toString().slice(2); // Anne sur deux chiffres
    const month = (today.getMonth() + 1).toString().padStart(2, "0"); // Mois sur deux chiffres
    const day = today.getDate().toString().padStart(2, "0"); // Jour sur deux chiffres
    return `${year}${month}${day}`; // Format YYMMDD
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!validationPossible) {
      alert(" Veuillez corriger les erreurs avant de valider.");
      return;
    }

    const adresseParDefaut = `VAINCRE LA MUCOVISCIDOSE\n181 RUE DE TOLBIAC\n75013 Paris\nfactures@vaincrelamuco.org`;

    // Vrifier si toutes les lignes ont une catgorie slectionne
    // const categorieManquante = lignesEngagement.some((row) => !row.categorie);

    let toutesLesLignes = [];

    if (Array.isArray(lignesEngagement)) {
      toutesLesLignes = lignesEngagement;
    } else {
      toutesLesLignes = Object.values(lignesEngagement).flat();
    }

    const categorieManquante = toutesLesLignes.some((row) => !row.categorie);

    if (categorieManquante) {
      alert("Toutes les lignes doivent avoir une catgorie slectionne.");
      return; // Bloquer la soumission
    }

    // Calculer le total gnral
    // const totalGeneral = lignesEngagement.reduce((acc, row) => acc + (row.total || 0), 0);

    const totalGeneral = toutesLesLignes.reduce(
      (acc, row) => acc + (row.total || 0),
      0
    );

    const montantInitialInconnu = rowBudgetsInitial.some(
      (montant) => montant === "0.00 "
    );

    if (montantInitialInconnu) {
      alert(
        "Le montant initial d'une ou plusieurs lignes est '0.00 ', validation non permise."
      );
      return; // Bloque la validation du formulaire
    }

    // Vrifier si le montant total est 0.00
    // if (totalGeneral === 0) {
    //   alert(
    //     "Le montant de la demande d'achat est de 0.00, validation non permise."
    //   );
    //   return; // Bloque la validation du formulaire
    // }

    const montantRestantNum = parseFloat(montantsBudget.montant_restant);
    // if (!isNaN(montantRestantNum) && totalGeneral > montantRestantNum) {
    //   alert("Le montant total de cette demande d'achat dpasse le montant restant disponible pour ce budget.");
    //   return;
    // }

    let projetNom = "";
    let projetMontant = "";

    try {
      const projetResponse = await fetch(
        `${racineAPI}projet.php?type=${typeDemande}`
      );
      const projetsData = await projetResponse.json();

      const projetAssocie = projetsData.find(
        (p) => p.budget === formData.budgetsActions
      );

      if (projetAssocie) {
        projetNom = projetAssocie.projet || "";
        projetMontant = projetAssocie.montant || "";
      }
    } catch (error) {
      console.warn(" Impossible de rcuprer les donnes de projet :", error);
    }

    // Rcuprer le demandeur depuis l'lment #demandeur
    const demandeurElement = document.getElementById("demandeur");
    const demandeur = demandeurElement
      ? demandeurElement.textContent
          .replace("Initiateur de la demande :", "")
          .trim()
      : "Inconnu";

    // Rcuprer Res_Id depuis l'URL
    const urlParams = new URLSearchParams(window.location.search);
    const resId = urlParams.get("Res_Id"); // Extraction de Res_Id
    const etat_action = urlParams.get("action");

    // Envoyer le Res_Id pour le traitement
    if (etat_action === "edit" && resId) {
      try {
        const resIdResponse = await fetch(`${racineAPI}traitement_res_id.php`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ resId }), // Envoyer le Res_Id dans le body
        });

        const resIdData = await resIdResponse.json();

        if (resIdData.status !== "success") {
          console.error(
            "Erreur lors du traitement du Res_Id :",
            resIdData.message
          );
        } else {
        }
      } catch (error) {
        console.error("Erreur lors de l'envoi du Res_Id :", error);
      }
    }

    try {
      // Rcupration et incrmentation de la squence
      const sequenceResponse = await fetch(
        `${racineAPI}generate_sequence.php`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            year: new Date().getFullYear(),
            preview: false,
          }), // preview:false pour incrmenter
        }
      );

      const sequenceData = await sequenceResponse.json();

      if (!sequenceData.sequence) {
        throw new Error(
          "Erreur lors de la rcupration de la squence : " +
            sequenceData.error
        );
      }

      // Construction du numro de pice avec la squence gnre
      const generatedNumeroPiece = `${
        polesMap[formData.services]
      }${getCurrentDate()}${sequenceData.sequence}`;

      // Ajout du budget initial et restant dans chaque ligne d'engagement
      const lignesEngagementAvecBudget = toutesLesLignes.map((ligne) => ({
        ...ligne,
        budgetInitial: ligne.budgetInitial || "0.00 ",
        budgetRestant: ligne.budgetRestant || "0.00 ",
      }));

      // Si un montant est connu, on le remplace
      const budgetInitialFormValue =
        montantsBudget.montant_initial !== "0.00 "
          ? montantsBudget.montant_initial
          : "0.00 ";

      const budgetRestantFormValue =
        montantsBudget.montant_restant !== "0.00 "
          ? montantsBudget.montant_restant
          : "0.00 ";

      // Mettre  jour formData avec l'adresse par dfaut si elles ne sont pas remplies
      const formDataUpdated = {
        ...formData,
        adresseLivraison: adresseParDefaut,
        adresseFacturation: adresseParDefaut,
        budgetInitial: budgetInitialFormValue,
        budgetRestant: budgetRestantFormValue,
      };

      let lignesEngagementFormat;

      if (formData.lignesTransversales) {
        // On est en mode pluriannuel
        lignesEngagementFormat = {};

        Object.entries(lignesEngagement).forEach(([annee, lignes]) => {
          lignesEngagementFormat[annee] = lignes.map((ligne) => ({
            ...ligne,
            annee: annee, //  Ajoute l'anne dans chaque ligne
          }));
        });
      } else {
        // Mode simple
        lignesEngagementFormat = lignesEngagementAvecBudget;
      }

      const urlParams = new URLSearchParams(window.location.search);
      console.log("URL params =", window.location.search);
      const coll_id = urlParams.get("Coll_Id");
      console.log("Coll_Id rcupr =", coll_id);

      // Prparer les donnes  soumettre avec le numro de pice mis  jour
      const dataSoumise = {
        ...formDataUpdated,
        lignesEngagement: lignesEngagementFormat,
        coll_id: coll_id,
        numeroPiece: generatedNumeroPiece, // Mise  jour du numro de pice
        demandeur,
        projetNom,
        projetMontant,
      };

      // @@@
      // Soumission des donnes
      const response = await fetch(`${racineAPI}process_form.php`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(dataSoumise),
      });

      const data = await response.json();

      if (data.status === "success") {
        setOpen(true); // Ouvre la popup de confirmation

        if (formData.pieceJointe && formData.pieceJointe.length > 0) {
          const formDataFile = new FormData();

          // Ajout de toutes les donnes du formulaire
          for (const key in formData) {
            if (formData.hasOwnProperty(key)) {
              formDataFile.append(key, formData[key]);
            }
          }

          // Ajout des fichiers
          for (let i = 0; i < formData.pieceJointe.length; i++) {
            formDataFile.append("pieceJointe[]", formData.pieceJointe[i]); // Ajouter chaque fichier
          }

          // Ajout du numro de pice
          formDataFile.append("numeroPiece", generatedNumeroPiece);
          formDataFile.append("totalGeneral", totalGeneral);
          formDataFile.append(
            "lignesEngagement",
            JSON.stringify(lignesEngagement)
          );

          try {
            const fileResponse = await fetch(`${racineAPI}upload_file.php`, {
              method: "POST",
              body: formDataFile, // Envoi du fichier avec toutes les donnes
            });

            const fileData = await fileResponse.json();

            if (fileData.status === "success") {
            } else {
              console.error(
                " Erreur lors de l'envoi des pices jointes :",
                fileData.message
              );
            }
          } catch (error) {
            console.error(
              " Erreur lors de l'upload des pices jointes :",
              error
            );
          }
        }
      } else {
        console.error(" Erreur lors de la soumission :", data.message);
        alert("Une erreur est survenue lors de la soumission.");
      }
    } catch (error) {
      console.error(" Erreur lors de la soumission :", error);
      alert("Une erreur est survenue.");
    }
  };

  const handleClose = () => {
    setOpen(false); // Ferme la popup
    if (window.opener) {
      window.close(); // Ferme la fentre si elle a t ouverte via JavaScript
    } else {
      alert("Cette fentre ne peut pas tre ferme automatiquement.");
    }
  };

  // const fetchBudgetInitial = async (annee, codePole, budget) => {
  //   try {
  //     const response = await fetch(`${racineAPI}affichage_budget_sur_da.php`, {
  //       method: "POST",
  //       headers: { "Content-Type": "application/json" },
  //       body: JSON.stringify({
  //         annee,
  //         code_pole: codePole,
  //         actions: budget
  //       }),
  //     });

  //     const data = await response.json();

  //     console.log(data.montant_initial);

  //     return data.montant_initial || "0.00 ";
  //   } catch (error) {
  //     console.error(
  //       " Erreur lors de la rcupration du budget initial :",
  //       error
  //     );

  //     return "0.00 ";
  //   }
  // };

    // const fetchBudgetRestant = async (annee, codePole, budget, categorie) => {
      
    //   try {
    //     const response = await fetch("https://armoires.zeendoc.com/vaincre_la_mucoviscidose/_ClientSpecific/66579/api/affichage_budget_restant_sur_da.php", {
    //       method: "POST",
    //       headers: { "Content-Type": "application/json" },
    //       body: JSON.stringify({ annee, code_pole: codePole, actions: budget, categorie }),
    //     });
    
    //     const data2 = await response.json();
    //     return data2.montant_restant || "0.00 ";
    //   } catch (error) {

    //     return "0.00 ";
    //   }
    // };

    // const fetchBudgetRestant = async (annee, codePole, budget) => {
    //   try {
    //     const response = await fetch(
    //       `${racineAPI}affichage_budget_restant_sur_da.php`,
    //       {
    //         method: "POST",
    //         headers: { "Content-Type": "application/json" },
    //         body: JSON.stringify({
    //           annee,
    //           code_pole: codePole,
    //           actions: budget,
    //         }),
    //       }
    //     );

    //     const data = await response.json();
    //     console.log(" Budget restant reu :", data);

    //     return data?.montant_restant ?? "0.00 ";
    //   } catch (error) {
    //     console.error("Erreur lors du fetch budget restant :", error);
    //     return "0.00 ";
    //   }
    // };


 


  return (
    <>
      {/* Barre verte */}
      <HeaderBar />

      {/* Contenu du formulaire */}
      <Paper
        elevation={3}
        sx={{
          padding: "20px",
          maxWidth: "90%",
          margin: "20px auto",
          borderRadius: "10px",
        }}
      >
        <form onSubmit={handleSubmit}>
          <Box sx={{ marginBottom: 2 }}>
            <Box sx={{ marginBottom: 2 }}>
              <img
                src={`${process.env.PUBLIC_URL}/vlm_logo.png`}
                alt="Logo"
                style={{ height: "100px" }}
              />
            </Box>
            <Box sx={{ display: "flex", justifyContent: "space-between" }}>
              <Typography variant="body1">
                <strong>Date :</strong> {today}
              </Typography>
              <Typography variant="body1" id="demandeur">
                <strong>Initiateur de la demande :</strong>{" "}
                {nom || "0.00 ..."}
              </Typography>
            </Box>
          </Box>

          {/* Section Adresses */}
          <AdresseSection formData={formData} handleChange={handleChange} />

          {/* Section Fournisseur */}
          <FournisseurSection
            formData={formData}
            handleChange={handleChange}
            setFormData={setFormData}
          />

          {/* Section Checkbox */}
          <CheckboxSection
            formData={formData}
            handleCheckboxChange={handleCheckboxChange}
          />

          {/* Section Proprit de la demande */}
          <ProprieteDemande
            formData={formData}
            handleChange={handleFormDataChange}
            setFormData={setFormData}
            // fetchBudgetInitial={fetchBudgetInitial}
            budgetInitial={budgetInitial}
            // fetchBudgetRestant={fetchBudgetRestant}
            budgetRestant={budgetRestant}
            setMontantsBudget={setMontantsBudget}
            montantsBudget={montantsBudget}
            budgetSelectionManuelle={budgetSelectionManuelle}
            setBudgetSelectionManuelle={setBudgetSelectionManuelle}
            categoriePrincipale={categoriePrincipale}
            setCategoriePrincipale={setCategoriePrincipale}
            typeDemande={typeDemande}
            setTypeDemande={setTypeDemande}
          />

          {/* Section Proprit de la demande */}
          <LignesEngagements
            filteredBudgetsInitial={filteredBudgets}
            formData={formData}
            handleChange={handleChange}
            selectedBudgetAction={formData.budgetsActions}
            selectedBudget={formData.budgetsActions}
            lignesEngagements={lignesEngagement}
            setLignesEngagement={setLignesEngagement}
            setFormData={setFormData}
            // fetchBudgetInitial={fetchBudgetInitial}
            budgetInitial={budgetInitial}
            // fetchBudgetRestant={fetchBudgetRestant}
            budgetRestant={budgetRestant}
            selectedPole={formData.services}
            initialBudgetsInitial={rowBudgetsInitial}
            onCategorieChange={setCategoriePrincipale}
            typeDemande={typeDemande}
          />

          <InformationLivraison
            formData={formData}
            handleChange={handleChange}
            handleFileChange={handleFileChange}
            selectedFiles={selectedFiles}
          />

          {/* Bouton de soumission */}
          <Box sx={{ textAlign: "center", marginTop: 3 }}>
            <Button
              type="submit"
              disabled={!validationPossible}
              variant="contained"
              color="primary"
              size="large"
              sx={{ textTransform: "none" }}
            >
              Soumettre
            </Button>
          </Box>
        </form>
      </Paper>

      {/* Popup */}
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Demande d'achat cre</DialogTitle>
        <DialogContent>
          <DialogContentText>
            La demande d'achat a t cre avec succs, elle est en cours de
            dpt sur le classeur.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Fermer la fentre
          </Button>
          {/* <Button onClick={handleDuplicate} color="secondary">
            Dupliquer la D.A.
          </Button> */}
        </DialogActions>
      </Dialog>
    </>
  );
};

export default FormulaireDemande;
