  // }, [formData?.budgetsActions]);

useEffect(() => {
  const fetchMontantProjet = async () => {
    if (!formData?.budgetsActions || !formData?.exerciceBudgetaire) {
      setMontantProjet(null);
      return;
    }

    try {
      // calcul de l'anne budgtaire  envoyer
      let anneeBudgetaire = formData.exerciceBudgetaire;

      if (formData.lignesTransversales) {
        const currentYear = parseInt(formData.exerciceBudgetaire, 10);
        const nextYear = currentYear + 1;
        anneeBudgetaire = `${currentYear}-${nextYear}`;
      }

      const response = await fetch(
        `${racineAPI}projet.php?type=${typeDemande}&annee=${encodeURIComponent(
          anneeBudgetaire
        )}&isPluriannuel=${formData.lignesTransversales ? 1 : 0}`
      );

      const data = await response.json();

      const matching = data.find((item) =>
        item.budgets.includes(formData.budgetsActions)
      );

      if (matching) {
        setMontantProjet(matching);
      } else {
        setMontantProjet(null);
      }
    } catch (error) {
      console.error("Erreur fetch montant projet :", error);
      setMontantProjet(null);
    }
  };

  fetchMontantProjet();
}, [
  formData?.budgetsActions,
  formData?.exerciceBudgetaire,
  formData?.lignesTransversales,
]);



  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(`${racineAPI}data.php`);
        const data = await response.json();

        console.log(" Donnes budgets charges", data);

        console.log(" Donnes budgets charges", data.data);
        setDataBudgets(data.data);
        setTypeDemande(data.type);

        console.log(
          " Type de demande en fonction du coll_id d'appel",
          data.type
        );
      } catch (error) {
        console.error(
          " Erreur lors du chargement initial des budgets :",
          error
        );
      }
    };

    fetchData();
  }, []);

  // --- Rcupration des ples depuis pole.php ---

  useEffect(() => {
    const fetchPoles = async () => {
      try {
        const response = await fetch(`${racineAPI}pole.php`);
        if (!response.ok) throw new Error(`Erreur HTTP: ${response.status}`);
        const data = await response.json();

        setPoles(data);
      } catch (error) {
        console.error("Erreur lors de la rcupration des ples :", error);
      }
    };
    fetchPoles();
  }, []);

  useEffect(() => {
    const fetchFilteredBudgets = async () => {
      if (!formData.services) return; // Vrifie si un ple est slectionn

      try {
        const response = await fetch(
          `${racineAPI}filtrage_budget_selon_pole2.php`,
          {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ selectedPole: formData.services }), // Envoie le ple slectionn
          }
        );

        const data = await response.json();

        if (data.status === "error") {
          console.error("Erreur :", data.message);
          setFilteredBudgets([]);
          return;
        }

        setFilteredBudgets(data);
      } catch (error) {
        console.error(
          "Erreur lors de la rcupration des budgets filtrs :",
          error
        );
        setFilteredBudgets([]);
      }
    };

    fetchFilteredBudgets();
  }, [formData.services]);

  // --- Initialisation du type de demande  "achat" par dfaut ---
  useEffect(() => {
    if (!formData.typeDemande) {
      setFormData((prev) => ({
        ...prev,
        typeDemande: "achat",
      }));
    }
  }, [formData.typeDemande, setFormData]);

  // --- Utilitaire pour gnrer la date au format AAAAMMJJ ---
  const getCurrentDate = () => {
    const now = new Date();
    const year = now.getFullYear();
    const month = String(now.getMonth() + 1).padStart(2, "0");
    const day = String(now.getDate()).padStart(2, "0");
    return `${year}${month}${day}`;
  };

  const handleBudgetChange = async (e) => {
    handleChange(e); //  garde le comportement normal
    if (e.target.name === "budgetsActions") {
      setBudgetSelectionManuelle(true); //  utilisateur a choisi un budget

      const selectedBudgetCode = e.target.value;

      //  Vrification uniquement si la case pluriannuelle est coche
      if (formData.lignesTransversales) {
        const annee1 = parseInt(formData.exerciceBudgetaire);
        const annee2 = annee1 + 1;

        try {
          const data = dataBudgets;

          const budgetAnnee1 = data.find(
            (item) =>
              item.annee === annee1.toString() &&
              item.budget.startsWith(selectedBudgetCode)
          );
          const budgetAnnee2 = data.find(
            (item) =>
              item.annee === annee2.toString() &&
              item.budget.startsWith(selectedBudgetCode)
          );
          console.log("annee 1 =>", budgetAnnee1);
          console.log("annee 2 =>", budgetAnnee2);

          if (!budgetAnnee1 || !budgetAnnee2) {
            alert(
              ` Le budget "${selectedBudgetCode}" n'est pas disponible pour :\n` +
                `${!budgetAnnee1 ? `- l'anne ${annee1}\n` : ""}` +
                `${!budgetAnnee2 ? `- l'anne ${annee2}` : ""}`
            );
            setValidationPossible(false); //  bloquer la validation
          } else {
            setValidationPossible(true); //  OK
          }
        } catch (error) {
          console.error(" Erreur lors du chargement des budgets :", error);
          alert("Erreur lors de la vrification des budgets pluriannuels.");
          setValidationPossible(false);
        }
      } else {
        // Si ce n'est pas une dpense pluriannuelle, ractiver la validation
        setValidationPossible(true);
      }
    }
  };

  // --- Gestion du changement de ple ---
  const handlePoleChange = async (e) => {
    const selectedPole = e.target.value;
    console.log(selectedPole);
    const poleNumber = (polesMap[selectedPole] || "0").toString();
    const datePart = getCurrentDate();

    try {
      const response = await fetch(`${racineAPI}generate_sequence.php`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          year: new Date().getFullYear(),
          preview: false,
        }),
      });
      if (!response.ok) throw new Error(`Erreur HTTP: ${response.status}`);
      const data = await response.json();
      if (data.sequence) {
        console.log(poleNumber);
        const generatedNumeroPiece = `${poleNumber}${datePart}${data.sequence}`;
        console.log(generatedNumeroPiece);
        setFormData((prev) => ({
          ...prev,
          services: selectedPole,
          numeroPiece: generatedNumeroPiece,
          typeDemande: "achat",
        }));
      
      } else {
        console.error(
          "Erreur lors de la rcupration de la squence :",
          data.error
        );
      }
    } catch (error) {
      console.error("Erreur lors de l'appel  generate_sequence.php :", error);
    }
  };

  useEffect(() => {
    const allReady =
      formData.exerciceBudgetaire &&
      formData.services &&
      formData.budgetsActions;

    if (allReady) {
      fetchMontantsBudget();
    }
  }, [formData.exerciceBudgetaire, formData.services, formData.budgetsActions]);


// const fetchMontantsBudget = async () => {
//   try {
//     const rawBudget = formData.budgetsActions || "";
//     const budgetCode = rawBudget.includes(" - ")
//       ? rawBudget.split(" - ")[0].trim()
//       : rawBudget.trim();

//     const payload = {
//       annee: formData.exerciceBudgetaire,
//       code_pole: formData.services,
//       actions: budgetCode,
//     };

//     const response = await fetch(`${racineAPI}total_budget.php`, {
//       method: "POST",
//       headers: {
//         "Content-Type": "application/json",
//       },
//       body: JSON.stringify(payload),
//     });

//     const data = await response.json();
//     console.log(" Donnes reues de total_budget.php :", data);

//     if (!data || data.error) {
//       console.warn(" Aucun budget trouv pour :", budgetCode, "anne :", payload.annee);
//       setMontantsBudget({ montant_initial: "0.00 ", montant_restant: "0.00 " });
//       return;
//     }

//     // le reste de ta logique inchang
//     setMontantsBudget({
//       montant_initial: data.montant_initial ?? "0.00 ",
//       montant_restant: data.montant_restant ?? "0.00 ",
//     });
